import React, { useState } from "react";
import Stat from "./components/Stat";
import "./App.css";
import Mana from "./img/mana.png";
import Health from "./img/health.png";
import Damage from "./img/damage.png";

const App = () => {
  const [mana, setMana] = useState(0);
  const [health, setHealth] = useState(0);
  const [damage, setDamage] = useState(0);

  const resetAll = () => {
    setMana(0);
    setHealth(0);
    setDamage(0);
  };

  return (
    <div className="App center-column">
      <h1 style={{ marginTop: "5px" }}>Shards Companion!</h1>
      <Stat name="Mana" img={Mana} value={mana} setValue={setMana} />
      <hr />
      <Stat name="Health" img={Health} value={health} setValue={setHealth} />
      <hr />
      <Stat name="Damage" img={Damage} value={damage} setValue={setDamage} />
      <hr />
      <div style={{ flexBasis: "60px" }}>
        <button
          style={{ width: "150px", height: "50px", fontSize: "20px" }}
          onClick={resetAll}
        >
          Reset all
        </button>
      </div>
    </div>
  );
};

export default App;
