import React from "react";
import "../App.css";
import "./Stat.css";

const Stat = (props) => {
  const increaseValue = () => {
    props.setValue(props.value + 1);
  };

  const decreaseValue = () => {
    if (props.value > 0) {
      props.setValue(props.value - 1);
    }
  };

  return (
    <div className="Stat center-column vertical-space">
      <h2 style={{ marginBottom: "10px" }}>{props.name}</h2>
      <div style={{ display: "flex", alignItems: "center" }}>
        <img
          src={props.img}
          alt={props.name}
          style={{ width: "120px", height: "120px", marginRight: "10px" }}
        ></img>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <button className="round" onClick={increaseValue}>
            +
          </button>
          <span style={{ margin: "8px 0", fontSize: "30px" }}>
            {props.value}
          </span>
          <button className="round" onClick={decreaseValue}>
            -
          </button>
        </div>
      </div>
    </div>
  );
};

export default Stat;
